#buybutton {
  color: #fff;
  cursor: pointer;
  text-transform: uppercase;
  background-color: #e93dc4;
  border: none;
  border-radius: 10px;
  padding: 15px 30px;
  font-size: 16px;
  font-weight: bold;
  transition: all .3s;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 10px #df53d877;
}

#buybutton:before {
  content: "";
  background: radial-gradient(circle, #fff3, #fff0);
  width: 300%;
  height: 300%;
  animation: 1.5s ease-out infinite destello;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#buybutton:hover {
  background-color: #ea05ff;
  animation: 1.5s ease-out infinite buyHover;
  transform: scale(1.1);
  box-shadow: 0 0 20px #ff00c8da;
}

@keyframes destello {
  0% {
    opacity: 0;
    transform: scale(1);
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    transform: scale(1.5);
  }
}

@keyframes buyHover {
  0% {
    background-color: #e93dc4;
    box-shadow: 0 0 10px #df53d877;
  }

  50% {
    background-color: #ea00ff;
    box-shadow: 0 0 20px #ff00c8da;
  }

  100% {
    background-color: #e93dc4;
    box-shadow: 0 0 10px #df53d877;
  }
}
